.CircularProgressbar {
  width: 13%;
  vertical-align: middle;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.sidebar {
  min-width: 250px;
  max-width: 250px;
  height: 100%;
  background: #235697;
  color: #fff;
  margin-left: -250px;
  transition: all 0.5s;
  position: fixed;
}

.sidebar.is-open {
  margin-left: 0;
  transition: .5s;
}


.sidebar-header {
  background: #235697;
  border-bottom: 2px solid white;
  padding-top:40px;
  padding-left: 15px;
  height: 100px;
  margin-bottom: 15px;
}

.sidebar-header h3{
  color: #fff;
  padding: 1em;
}

.sidebar ul p {
    color: #fff;
    padding: 10px;
}

.menu-open {
  background: #235697;
}

.nav-item:hover {
    color: #235697;
    background: #fff;
}
.nav-item:active {
  color:#235697;
  background: white;
}
.items-menu {
  color: #fff;
  background: #235697;
}

a.active{
  color: #235697 !important;
  background: #fff !important;
}
li a.dropdown-toggle::after {
    display: inline-flex;
    position: relative;
    left: 60%;
    top: 10%
}

.sidebar-header > span {
    position: relative;
    float: right;
    margin: .5em;
    font-size: 2rem;
    cursor: pointer;
    display: none;
}
.side-menu {
  height: calc(100vh - 130px);
  overflow-y: scroll;
}

.side-menu::-webkit-scrollbar {
  width: 10px;
}

.side-menu::-webkit-scrollbar-thumb {
  background: #5466B3; 
  border-radius: 3px;
}

.side-menu::-webkit-scrollbar-thumb:hover {
  background: #3A4C99; 
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
.content {
  padding: 20px;
  margin-left: 0;
  height: 100vh;
  /* position: relative; */
  transition: all 0.5s;
}
.content.is-open{
  margin-left: 250px;
  transition: all 0.5s;
}
.content-area{
  margin-left: 0;
  /* height: 88.1vh; */
  box-shadow: 10px 10px 58px 26px rgba(197,197,199,0.53);
}

@media only screen and (max-width: 500px) {

    .content.is-open {
      margin-left: 100%;
    }

    .sidebar.is-open {
      min-width: 100%;
      max-width: 100%;
      margin-left: 0;
      transition: all 0.5s, height 0s;
    }

    .sidebar.is-open > .sidebar-header span{
      display: unset;
    }

    li a.dropdown-toggle::after {
        display: inline-block;
        position: relative;
        left: 68%;
    }
    .logo{
      display: none;
    }
}

